.feed-bottom{
  display: flex;
  margin-bottom: 4vw;
  margin-top: 4vw;
}


.feed-header{
 padding: 1vw;
 border-top-left-radius: 2vw;
 border-top-right-radius: 2vw;
 background-color: #e1e1e1;
}

.feed-box{
  width: 45vw;
  /* height: 40vw; */
  margin-left: 3.5vw;
  margin-top: 3.5vw;
  border-radius: 2vw;
  background-color: white;
  filter: drop-shadow(0px 0px 0.5vw #999999);
  text-align: center;
  border: 2px solid #e1e1e1;
}

.feed-members{

}

.members-box {
  display: flex;
  margin: 2.5%;
  width: 95%;
  height: 60px;
  background-color: #e1e1e1;
  border-radius: 1vw;
  text-align: left;
  filter: drop-shadow(0px 0px 0.5vw #999999);
}
.member-pic{
  padding: 1vw;
  border-radius: 100vw;
}
.member-name{
  width: 100%;
  margin: auto;
  padding-left: 1vw;
}

.feed-photos{
  height: 40vw;
}


.poster-box{
  width: 90%;
  /* height: 20vw; */
  margin: 5%;
  border-radius: 2vw;
  background-color: white;
  /* filter: drop-shadow(0px 0px 0.5vw black); */
  border: 2px solid #e1e1e1;
  text-align: left;
    filter: drop-shadow(0px 0px 0.5vw #999999);
}

.post-header{
  display:flex;
  width: 100%;
  height: 60px;
  text-align: left;
  margin-left: 0;
}

.poster-name{
  padding: 1vw;
}

.poster-pic{
   display: flex;
   border-radius: 10vw;
   margin: 0px;
   padding: 1vw;
}

.post-body{
  padding: 1vw;
}
